import { lazy, useLayoutEffect, useState } from "react";
import { Route, Routes, useLocation } from "react-router-dom";
import { ToastContainer } from "react-toastify";
import { ScrollToTops } from "../ScrollToTop/ScrollToTop";

import "../../styles/app.scss";

import AOS from "aos";
AOS.init();

const Header = lazy(() => import("../Header/Header"));
const Hero = lazy(() => import("../Hero/Hero"));
const News = lazy(() => import("../News/News"));
const OnePageNews = lazy(() => import("../News/OnePageNews"));
const Courses = lazy(() => import("../Courses/Courses"));
const Tests = lazy(() => import("../Tests/Tests"));
const Contact = lazy(() => import("../Contact/Contact"));
const Footer = lazy(() => import("../Footer/Footer"));
const Teachers = lazy(() => import("../Teachers/Teacher"));
const Page404 = lazy(() => import("../404/404"));
const Modal = lazy(() => import("../Modal/Modal"));

const Wrapper = ({ children }) => {
  const location = useLocation();
  useLayoutEffect(() => {
    document.documentElement.scrollTo(0, 0);
  }, [location.pathname]);
  return children;
};

const App = () => {
  const [modalVisible, setModalVisible] = useState(false);
  const onClickOpenModal = () => {
    const body = document.body;
    if (modalVisible) {
      body.style.overflowY = "visible";
    } else {
      body.style.overflowY = "hidden";
      body.addEventListener("keydown", (e) => {
        if (e.key === "Escape") {
          setModalVisible(false);
          body.style.overflowY = "visible";
        }
      });
    }
    setModalVisible(!modalVisible);
  };

  return (
    <Wrapper>
      <Header />
      <Routes>
        <Route
          path={"/"}
          element={
            <>
              <Hero onClickOpenModal={onClickOpenModal} />
              <News homePage={true} />
              <Courses onClickOpenModal={onClickOpenModal} />
              <Teachers />
              <Tests />
              <Contact />
            </>
          }
        />
        <Route path={"/news"} element={<News activeNews={true} />} />
        <Route path={"/news/:id/:title"} element={<OnePageNews />} />
        <Route
          path={"/courses"}
          element={
            <Courses onClickOpenModal={onClickOpenModal} activeCourses={true} />
          }
        />
        <Route path={"/tests"} element={<Tests activeTest={true} />} />
        <Route path={"/contact"} element={<Contact activeContact={true} />} />
        <Route
          path={"/teachers"}
          element={<Teachers activeTeachers={true} />}
        />

        <Route path={"*"} element={<Page404 />} />
      </Routes>
      <Footer />
      {modalVisible ? <Modal onClickOpenModal={onClickOpenModal} /> : ""}
      <ToastContainer />
      <ScrollToTops />
    </Wrapper>
  );
};

export default App;
